html {
  font-size: 12px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #3E6F8E;
  color: #333;
  font-family: 'Advent Pro', sans-serif;
}

p {
  font-size: 1.8rem;
}

small {
  font-size: 1.6rem;
}

.container {
  margin: 0 auto;
  margin-top: 30px;
  max-width:80vw;
  padding: 40px;
}

.movie-box {
  background-color: #f3f7f1;
  padding: 30px;
  border-radius: 50px;
}

.title {
  font-size: 7rem;
  text-align: center;
  color: #1D3557;
}

.form {
  display: grid;
}

.label {
  font-size: 1.6rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  font-weight: 600;
}

.input {
  font-size: 1.8rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
}

.button {
  background-color: #EB5C68;
  color: black;
  padding: 1rem 2rem;
  border: 1px solid rgba(0,0,0,0.75);
  border-radius: 20px;
  font-size: 1.6rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: #E63946;
}

@media (max-width:786px){
  .form {
      grid-template-columns: auto 1fr auto;
      grid-gap: 1rem;
      align-items: center;
  }
  
  .input {
      margin-bottom: 0;
  }

  .card--image {
    min-width: 100px;
  }
}

.card-list {
  margin-top: 4rem;
}

.card {
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: -2px -2px 10px #1D3557;
  margin-bottom: 2rem;
  background-color: #F0F9F9;
  display: flex;
  align-content: space-between;
}

.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}

.card--content {
  margin-left: 1.5rem;
  font-weight: 600;
}

.card--image {
  min-width: 250px;
}